const VendorLogin = ({ user }) => {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');

    // Firebase requires an email format, so we'll create a dummy email from the username
    const formatEmail = (user) => `${user}@shopnext.dev`;

    const handleSignUp = async (e) => {
        e.preventDefault();
        setError('');
        try {
            const email = formatEmail(username);
            await createUserWithEmailAndPassword(auth, email, password);
            // User will be auto-logged in by onAuthStateChanged
        } catch (err) {
            setError(err.message);
        }
    };

    const handleSignIn = async (e) => {
        e.preventDefault();
        setError('');
        try {
            const email = formatEmail(username);
            await signInWithEmailAndPassword(auth, email, password);
        } catch (err) {
            setError(err.message);
        }
    };
    
    return (
        <div className="flex items-center justify-center min-h-[60vh]">
            <div className="w-full max-w-md p-8 space-y-6 bg-white rounded-lg shadow-md border">
                <h2 className="text-2xl font-bold text-center text-gray-800">Vendor Login</h2>
                <form className="space-y-4">
                    <div><label className="text-sm font-medium text-gray-700">Username</label><input type="text" value={username} onChange={(e) => setUsername(e.target.value)} placeholder="e.g., ElectroGadgets" className="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" /></div>
                    <div><label className="text-sm font-medium text-gray-700">Password</label><input type="password" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="••••••••" className="w-full px-4 py-2 mt-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500" /></div>
                    {error && <p className="text-sm text-red-500">{error}</p>}
                    <div className="flex space-x-2">
                        <button onClick={handleSignIn} className="w-full py-3 font-semibold text-white bg-blue-600 rounded-full hover:bg-blue-700 transition-colors flex items-center justify-center"><LogIn className="h-5 w-5 mr-2"/>Sign In</button>
                        <button onClick={handleSignUp} className="w-full py-3 font-semibold text-blue-600 bg-blue-100 rounded-full hover:bg-blue-200 transition-colors">Sign Up</button>
                    </div>
                </form>
                <p className="text-xs text-center text-gray-500">For this demo, you can Sign Up to create an account.</p>
            </div>
        </div>
    );
};

