const VendorArea = ({ setView, user }) => {
    const [vendorView, setVendorView] = useState('dashboard'); // 'dashboard' or 'create'
    const renderContent = () => {
        if (!user) return <VendorLogin />;
        switch (vendorView) {
            case 'create': return <CreateProductForm setVendorView={setVendorView} />;
            case 'dashboard':
            default: return <VendorDashboard setVendorView={setVendorView} user={user} setView={setView} />;
        }
    };
    return (<main className="container mx-auto px-4 py-8">{renderContent()}</main>);
};


// --- MAIN APP COMPONENT ---

export default function App() {
  const [view, setView] = useState({ page: 'home', id: null });
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredProducts, setFilteredProducts] = useState(mockProducts);
  const [user, setUser] = useState(null); // To hold the authenticated user object
  const [loading, setLoading] = useState(true); // To wait for auth state check

  // Listen for authentication state changes
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      setLoading(false);
    });
    return () => unsubscribe(); // Cleanup subscription on component unmount
  }, []);
  
  useEffect(() => {
    const handleSearch = () => {
      if (searchTerm.trim() === '') { setFilteredProducts(mockProducts); } 
      else {
        const lowercasedQuery = searchTerm.toLowerCase();
        const results = mockProducts.filter(p => p.name.toLowerCase().includes(lowercasedQuery) || p.subtitle.toLowerCase().includes(lowercasedQuery) || p.tags.some(t => t.toLowerCase().includes(lowercasedQuery)) || p.vendor.toLowerCase().includes(lowercasedQuery) || p.shortDescription.toLowerCase().includes(lowercasedQuery));
        setFilteredProducts(results);
      }
      if (searchTerm.trim() !== '') { setView({ page: 'home', id: null }); }
    };
    handleSearch();
  }, [searchTerm]);

  const renderView = () => {
    switch (view.page) {
      case 'product':
        const product = mockProducts.find(p => p.id === view.id);
        return product ? <ProductPage product={product} setView={setView} /> : <HomePage products={filteredProducts} setView={setView} />;
      case 'vendor':
        return <VendorArea setView={setView} user={user} />;
      case 'home':
      default:
        return <HomePage products={filteredProducts} setView={setView} />;
    }
  };

  if (loading) {
      return <div className="min-h-screen flex items-center justify-center">Loading...</div> // Or a spinner component
  }

  return (
    <div className="bg-gray-50 min-h-screen font-sans">
        <Header setView={setView} searchTerm={searchTerm} setSearchTerm={setSearchTerm} user={user} />
        {renderView()}
        <Footer />
    </div>
  );
}

